<template>
  <div id="app">
    <DocumentationHeader></DocumentationHeader>
    <DocumentationDemo></DocumentationDemo>
    <DocumentationFeatures></DocumentationFeatures>
    <DocumentationContribute></DocumentationContribute>
    <DocumentationFooter></DocumentationFooter>
    <router-view/>
  </div>
</template>

<script>
import DocumentationHeader from "./components/DocumentationHeader";
import DocumentationDemo from "./components/DocumentationDemo";
import DocumentationFooter from "./components/DocumentationFooter";
import DocumentationContribute from "./components/DocumentationContribute";
import DocumentationFeatures from "./components/DocumentationFeatures";
export default {
  name: 'App',
  components: {
    DocumentationFeatures,
    DocumentationContribute, DocumentationFooter, DocumentationDemo, DocumentationHeader}
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>



